@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200;1,300&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #223343;
}

header {
  color: whitesmoke;
  padding: 50px 15px 0px;
}
header h1 {
  font-size: 300;

  text-align: center;
}

.searchbox {
  width: 50%;
  margin: 5px;
  padding: 15px;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  padding: 15px;
  border: none;
  outline: none;
  font-size: 20px;
  font-weight: 350;
  border-radius: 8px;
  transition: 0.4 ease-out;
  display: flex;
  justify-content: center;
}

button {
  border: none;
}

.searchbox-wrapper {
  display: flex;
  justify-content: center;
  margin-left: 80px;
  align-items: center;
  width: 90%;
}

.movie img {
  max-width: 100%;
}

.title {
  color: honeydew;
}

.container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.movie {
  width: 180px;
  margin: 1rem;
  background-color: rgb(4, 48, 97);
  border-radius: 3px;
  box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
}

.votes {
  background-color: #223343;
  color: white;
  padding: 4px;
  margin: 0;
  border-radius: 10%;
  font-weight: bolder;
}
.items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.info {
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  font-weight: bold;
  padding: 1rem;
  transform: translateY(103%);
  border-top-left-radius: 10%;
  border-top-right-radius: 10%;
  overflow: auto;
  max-height: 100%;
}

.movie:hover .info {
  transform: translateY(0%);
  transition: 0.5s ease-in-out;
}

.green {
  color: rgb(29, 201, 29);
}
.red {
  color: red;
}
.yellow {
  color: yellow;
}
